import { FiGithub, FiMapPin, FiMail } from 'react-icons/fi';

const contacts = [
  {
    id: 0,
    name: 'Санкт-Петербург',
    icon: <FiMapPin />,
  },
  {
    id: 1,
    name: 'github.com/izquierrrdo',
    link: 'https://github.com/izquierrrdo',
    icon: <FiGithub />,
  },
  {
    id: 2,
    name: 'izquierrrdo@gmail.com',
    link: 'mailto:izquierrrdo@gmail.com',
    icon: <FiMail />,
  },
];

const ContactDetails = () => {
  return (
    <div className="w-full lg:w-1/2">
      <div className="text-left max-w-xl px-6">
        <h2 className="font-general-medium text-2xl text-primary-dark dark:text-primary-light mt-12 mb-8">
          Контакты
        </h2>
        <ul className="font-general-regular">
          {contacts.map((contact) => (
            <li className="flex " key={contact.id}>
              <i className="text-2xl text-gray-500 dark:text-gray-400 mr-4">
                {contact.icon}
              </i>
              {contact.link ? (
                <a href={contact.link} className="text-lg mb-4">
                  {contact.name}
                </a>
              ) : (
                <span className="text-lg mb-4 text-ternary-dark dark:text-ternary-light">
                  {contact.name}
                </span>
              )}
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
};

export default ContactDetails;
